
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.service {
  position: relative;
  overflow: hidden;
  background: #f9fbfe url(~@/assets/images/dashboard/service_bg_wave.png) center center no-repeat;
  background-size: 100% auto;
  .inner {
    min-width: 12.3rem;
  }
  &-list {
    $list: &;

    display: flex;
    justify-content: space-between;
    padding-top: 10rem;
    &__item {
      width: 36.2rem;
      padding: 3.2rem;
      border-radius: $G-box-radius;
      background-color: #fff;
      box-shadow: $G-box-shadow;
      &--report {
        background-color: #584aff;
        box-shadow: 0 0.3rem 1.7rem rgba(#584aff, 0.7);
        #{$list}-body {
          .spec-list {
            &__item {
              &::after {
                background-image: svgIcon(check, 4d34ef);
              }
            }
          }
        }
      }
      &--dashboard {
        background-color: #a44aff;
        box-shadow: 0 0.3rem 1.7rem rgba(#a44aff, 0.7);
        #{$list}-body {
          .spec-list {
            &__item {
              &::after {
                background-image: svgIcon(check, 6f00e4);
              }
            }
          }
        }
      }
      &[class*="service-list__item--"] {
        #{$list}-header {
          .type {
            color: #fff;
          }
          .tit {
            color: #fff;
          }
        }
        #{$list}-body {
          border-color: rgba(#fff, 0.16);
          .spec-list {
            &__item {
              color: #fff;
              &::after {
                background-color: #fff;
              }
            }
          }
        }
      }
    }
    &-header {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 3rem;
      height: 27.2rem;
      .tag {
        display: flex;
        align-items: center;
        position: absolute;
        top: -3.2rem;
        left: 50%;
        height: 5.5rem;
        padding: 0 3.2rem;
        border-radius: 2em;
        color: #fff;
        font-weight: 500;
        background-color: #000;
        transform: translate(-50%, -50%);
        &:empty {
          display: none;
        }
      }
      .type {
        position: absolute;
        top: 1rem;
        left: 0;
        font-size: 2.5rem;
        font-weight: 600;
        color: $G-color-primary;
      }
      .tit {
        font-size: 3.5rem;
        font-weight: bold;
        color: #535258;
        text-align: center;
        .num {
          font-size: 6.4rem;
        }
      }
    }
    &-body {
      height: 27rem;
      padding-top: 3rem;
      border-top: 1px solid rgba(#000000, 0.16);
      .spec-list {
        &__item {
          position: relative;
          margin-bottom: 1.8rem;
          padding-left: 4.4rem;
          color: #6b6b6b;
          &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 2.4rem;
            height: 2.4rem;
            border-radius: 100%;
            background-color: $G-color-primary;
            background-image: svgIcon(check, fff);
            background-size: 40%;
            transform: translateY(-50%);
          }
          .tit {
            font-size: 2rem;
            font-weight: bold;
          }
          .desc {
            font-size: 1.6rem;
            font-weight: 500;
            &::before {
              content: "\00B7";
              display: inline-block;
              padding: 0 0.8rem 0 0.5rem;
            }
          }
        }
      }
    }
    &-footer {
      text-align: center;
      .btn:not(.btn-round-border) {
        &::after {
          background: #000;
        }
      }
    }
  }
}
