
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.video-button {
  $v: &;

  overflow: hidden;
  position: relative;
  width: 117rem;
  height: 65rem;
  border-radius: $G-box-radius;
  box-shadow: $G-box-shadow;
  &__thumb {
    position: relative;
    width: 100%;
    height: 100%;
    background-size: cover;
    @include transition($time: 0.25);
    &::after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 0;
      background-color: #000;
      opacity: 0.5;
    }
  }
  &:hover {
    #{$v}__thumb {
      transform: scale(1.05);
    }
  }
}
