
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.partner {
  padding: 10rem 0 20rem;
  .inner {
    width: 132rem;
  }
  &__title {
    font-size: 4.8rem;
    font-weight: bold;
    text-align: center;
    line-height: 1em;
    margin-bottom: 13rem;
  }
  &-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
      width: auto;
      max-height: 4rem;
    }
  }
}
.swiper {
  position: relative;  
  &-slide {
    display: inline-flex;    
    height: auto;
    .logo { 
      width: 80%;
    }
  }
}
