
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.news {
  background-color: #f8fafc;
  &-list {
    margin-bottom: 5.2rem;
    &:empty {
      &::after {
        [data-loding-spinner="false"] & {
          content: " .";
          display: flex;
          justify-content: center;
          align-items: center;
          height: 10rem;
          padding: 0 3.5rem;
          font-size: 2.3rem;
          font-weight: bold;
          color: #ccc;
          border-radius: $G-box-radius;
          background-color: #fff;
          box-shadow: 0 0 0.8rem rgba(7, 7, 7, 0.08);
        }
      }
    }
    &__item {
      padding: 1rem 0;
    }
    &__link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 10rem;
      padding: 0 3.5rem;
      font-size: 2.3rem;
      color: #505050;
      border-radius: $G-box-radius;
      background-color: #fff;
      box-shadow: 0 0 0.8rem rgba(7, 7, 7, 0.08);
      &:hover {
        color: #050505;
      }
      .txt {
        width: 85%;
      }
      .date {
        font-size: 2rem;
        color: #a3a3a3;
      }
    }
  }
  &-pagination {
    $p: &;

    display: flex;
    justify-content: center;
    &__item {
      padding: 0 1rem;
    }
    &__btn {
      font-size: 2rem;
      font-weight: 500;
      color: #b4b4b4;
      @include transition;

      .is-active & {
        color: #6794f7;
      }
      .is-disable & {
        pointer-events: none;
        opacity: 0.3;
      }
    }
  }
}
