
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.faq {
  background: #746bf0 url("~@/assets/images/contact/faq_bg.jpg") center center no-repeat;
  background-size: 100% auto;
  .l-section__title {
    color: #fff;
  }
  &-list {
    $headerH: 10rem;

    &:empty {
      &::after {
        content: " .";
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: $headerH;
        font-size: 2.4rem;
        color: #ccc;
        background-color: #fff;
        border-radius: $G-box-radius;
      }
    }
    &__item {
      padding: 1rem 0;
    }
    &-wrapper {
      overflow: hidden;
      background-color: #fff;
      border-radius: $G-box-radius;
    }
    &-header {
      position: relative;
      .btn-tit {
        justify-content: flex-start;
        height: $headerH;
        width: 90%;
        padding-left: 4rem;
        font-size: 2.6rem;
        font-weight: bold;
        color: #586afa;
        text-align: left;
        @include transition($tgs: color);

        &:hover {
          color: $G-color-active;
        }
        &::before {
          content: "Q.";
          display: inline-block;
          margin-right: 0.5rem;
        }
        &:empty {
          &::before {
            color: #ccc;
          }
          &::after {
            content: " .";
            color: #ccc;
          }
        }
      }
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 3.5rem;
        width: 1.5rem;
        height: 1.5rem;
        background-image: svgIcon(triangle-down, 5184F6);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 100%;
        transform: translateY(-50%);
        transform-origin: 50% 25%;
        .is-active & {
          transform: rotate(180deg);
        }
      }
    }
    &-body {
      overflow: hidden;
      border-top: 1px solid #c4c4c4;
      &__textarea {
        padding: 4rem 6rem;
        font-size: 2.4rem;
        line-height: 2em;
        word-break: keep-all;
        &:empty {
          &::after {
            content: " .";
            color: #ccc;
          }
        }
      }
    }
  }
}
