
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.send-mail {
  background-color: #f8fafc;
}
.form {
  $fo: &;

  padding: 4.5rem 8rem 4.8rem;
  border-radius: $G-box-radius;
  background-color: #fff;
  box-shadow: $G-box-shadow;
  &-fieldset {
    $fi: &;

    &[data-fieldset=""] {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -1.75rem;
      #{$fi}__title {
        flex-basis: 100%;
        margin-bottom: 3.6rem;
        padding-left: 1.75rem;
        font-size: 3.2rem;
        font-weight: bold;
      }
      #{$fo}-row {
        flex: 1 1 50%;
        height: 7.8rem;
        margin-bottom: 2.5rem;
        padding: 3.2rem 1.75rem 0;
        .form-text__label {
          display: flex;
          align-items: center;
          position: absolute;
          top: 0;
          font-size: 1.8rem;
          .txt {
            margin-right: 1rem;
            &::after {
              content: "*";
              display: inline-block;
              padding-left: 0.3rem;
              color: #f24e1e;
            }
          }
          .error {
            font-size: 1.2rem;
            color: #f24e1e;
          }
        }
      }
    }
    &[data-fieldset=""] {
      #{$fo}-row {
        &[data-row=""] {
          .form-check__input {
            margin-right: 1rem;
            border-radius: 100%;
            background-size: 50%;
          }
          .form-check__label {
            strong {
              text-decoration: underline;
            }
          }
          .error {
            position: absolute;
            top: 2.8rem;
            left: 0;
            font-size: 1.2rem;
            color: #f24e1e;
          }
        }
      }
    }
  }
}
