
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.swiper-main:deep() {
  position: relative;
  height: 64rem;
  width: 100rem;
  margin: 0 0 6rem auto;
  transform: translateX(35rem);
  .swiper--title {
    .swiper-slide {
      font-size: 3rem;
      text-align: center;
      color: #525252;
      opacity: 0 !important;
      &-duplicate-active {
        opacity: 1 !important;
      }
    }
  }
  .swiper--thumb {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 84.3rem;
    height: 50.7rem;
    border-radius: 2rem;
    box-shadow: $G-box-shadow;
    transform: translate(-50%, -50%) skewX(0.001deg);
    .swiper-slide {
      background-size: cover;
    }
  }
  .swiper-button-black {
    color: #adadad;
    &:after {
      font-size: 4.4rem;
    }
    @include transition;

    &:hover {
      color: $G-color-hover;
    }
  }
  .swiper-pagination-bullet {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    font-family: "Jost";
    font-style: normal;
    font-size: 1.8rem;
    font-weight: bold;
    color: #fff;
    line-height: 1em;
    opacity: 1;
    background: #bbbbbb;
    @include transition;

    &-active {
      background-color: $G-color-hover;
    }
  }
}
