
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.graph-swiper:deep() {
  position: relative;
  padding: 10rem 0;
  .swiper {
    width: 108rem;
    height: 56rem;
    margin-bottom: 4rem;
    border-radius: $G-box-radius;
    box-shadow: $G-box-shadow;
  }
  .swiper-slide {
    background-size: cover;
  }
  .swiper-button {
    position: absolute;
    top: calc(50% - 4rem);
    left: 50%;
    width: 105%;
    transform: translate(-50%, -50%);
    &-black {
      color: #adadad;
      &:after {
        font-size: 4.4rem;
      }
      @include transition;

      &:hover {
        color: $G-color-hover;
      }
    }
  }
  .swiper-pagination {
    position: relative;
    top: 0;
    left: 0;
    bottom: 0;
    &-bullet {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 4rem;
      height: 4rem;
      font-family: "Jost";
      font-style: normal;
      font-size: 1.8rem;
      font-weight: bold;
      color: #fff;
      line-height: 1em;
      opacity: 1;
      background: #bbbbbb;
      @include transition;

      &-active {
        background-color: $G-color-hover;
      }
    }
  }
}
