
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

/* footer */
.footer {
  $footerH: 37.5rem;

  position: relative;
  height: $footerH;
  &-inner {
    position: fixed;
    bottom: 0;
    width: 100%;
    min-width: $G-inner-width;
    height: $footerH;
    transform: translateY(10rem);
  }
  &-overlay {
    position: fixed;
    bottom: 0;
    z-index: 2;
    width: 100%;
    height: $footerH;
    background-color: #ccc;
    pointer-events: none;
  }
  &-bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    background-color: #f9f9f9;
  }
}

// 
.about {
  display: flex;
  justify-content: space-between;
  padding-bottom: 1.5rem;
  margin-bottom: 6.2rem;
  border-bottom: 0.1rem solid #d0d1e5;
  .logo {
    font-size: 3.6rem;
  }
  &-list {
    display: flex;
    align-items: center;
    &__link {
      margin-left: 6rem;
      font-size: 1.5rem;
      font-weight: bold;
      color: #4d4d5b;
    }
  }
}

// 
.contact {
  font-size: 1.5rem;
  color: #4d4d5b;
  text-align: center;
  &-address {
    margin-bottom: 0.5rem;
  }
  &-list {
    display: inline-flex;
    &__txt {
      position: relative;
      margin-bottom: 2rem;
      padding: 0 1rem;
      letter-spacing: 0.02em;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        height: 1.1rem;
        width: 0.1rem;
        background-color: #4d4d5b;
        transform: translateY(-50%);
      }
      &:first-child::after {
        display: none;
      }
    }
  }
}
