
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.modal {
  $p: &;

  &-wrapper {
    overflow: hidden;
    border-radius: 2rem;
  }
  &-body {
    position: relative;
    margin: 2rem 0 2rem 2rem;
    padding-right: 2rem;
    &:empty {
      width: 40rem;
      height: 20rem;
      margin: 2rem;
      padding-right: 0;
      &::after {
        content: "URL  Type  ";
        position: absolute;
        top: 50%;
        left: 50%;
        font-weight: bold;
        color: #ccc;
        white-space: nowrap;
        transform: translate(-50%, -50%);
      }
    }
    .youtube {
      width: 91.4rem;
      height: 51.4rem;
    }
  }
  &-header {
    display: flex;
    align-items: center;
    height: 6rem;
    background: $G-grad-primary;
    &__title {
      width: 90%;
      padding-left: 1em;
      font-size: 2.3rem;
      font-weight: bold;
      line-height: 1em;
      color: #fff;
      text-shadow: $G-text-shadow;
    }
  }
  &-enter-active,
  &-leave-active {
    transition: none 2s;

    #{$p}-wrapper {
      @include transition($tgs: opacity transform, $time: 0.2, $delay: 0.2);
    }
    #{$p}-dimmed {
      @include transition($tgs: opacity, $time: 0.2);
    }
  }
  &-leave-active {
    @include transition($tgs: opacity);
  }
  &-enter,
  &-leave-to {
    transition: none 1s;
    #{$p}-wrapper {
      opacity: 0;
      transform: translate(-50%, -45%);
      @include transition($tgs: opacity transform, $time: 0.4);
    }
    #{$p}-dimmed {
      opacity: 0;
      @include transition($tgs: opacity, $time: 0.4, $delay: 0.4);
    }
  }
}
