
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

.analysis {
  overflow: hidden;
  padding: 10rem 0 20rem;
  .inner {
    min-width: 160rem;
  }
  &-thumb {
    display: block;
    width: 108rem;
    height: auto;
    margin: 0 auto;
  }
  &-service {
    padding-top: 25rem;
    &__title {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 5rem;
      font-size: 4.4rem;
      font-weight: bold;
      .logo {
        font-size: 6.5rem;
        margin-right: 3rem;
      }
    }
    &__description {
      margin-bottom: 5rem;
      font-size: 3.5rem;
      font-weight: bold;
      text-align: center;
    }
    &-list {
      display: flex;
      justify-content: space-around;
      border-top: 0.2rem solid #8f93a3;
      &__item {
        padding-top: 5rem;
        text-align: center;
        word-break: keep-all;
        .tit {
          font-size: 3.5rem;
          font-weight: bold;
          color: #0283f2;
        }
        .desc {
          font-size: 2.5rem;
          font-weight: 500;
          color: #666666;
        }
      }
    }
  }
}
