
            @import "@/assets/scss/utils/_variables.scss";
            @import "@/assets/scss/utils/_mixins.scss";
            @import "@/assets/scss/utils/_functions.scss";
            @import "@/assets/scss/components/_icons.scss";
            

/* visual */

.visual {
  $v: &;

  overflow: hidden;
  position: relative;
  height: 82.6rem;
  background-color: #eaf2f9;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url("~@/assets/images/main/visua_bg.png") center center no-repeat;
    background-size: auto 100%;
  }
  .test {
    position: absolute;
    top: 0;
    left: 0;
    width: 300px;
    height: 300px;
    border: 10px solid red;
    z-index: 10;
  }
  &-enter-active,
  &-leave-active {
    transition-delay: 4s;
    #{$v}-thumb {
      @include transition($tgs: transform, $delay: $G-container-delay + 0.5);
    }
  }
  &-enter,
  &-leave-to {
    #{$v}-thumb {
      transform: translate(50.5rem, -50%);
    }
  }
}
